@import '../../common/sass/mixins.scss';

.mainWrapper {

  .container {
    @include flexColumnCenter();
    background-color: white;
    padding: 20px 0;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;


    .text {
    }

    .iconsBlock {
      width: 250px;
      display: flex;
      justify-content: space-around;
      text-align: center;
      margin: 15px 0 15px 0;

      .iconWrapper {
        width: 35px;
        height: 35px;
        background-size: cover;
        padding: 2px 0;

        .icon {
          max-width: 100%;
          //height: 102%;
          object-fit: contain;
          @include flexStandardCentering();
          background-position: center;
          position: relative;
          margin-bottom: 10px;
          opacity: 65%;
          border-radius: 5px;
          transition: opacity 0.2s;

          &:hover {
            opacity: 80%;
          }
        }

        .git {
          //padding-top: 0.3px;
          height: 111%;
        }
        .telegram {
          //height: ;
        }

      }
    }
  }

}

